import "./blog.css";
import ReactHtmlParser from "react-html-parser";
import Link from "next/link";
import cardTitle from "./cardTitle";
import Loader from "../Loader/loader";
import BackButton from "../BackButton/backButton";
import Footer from "../Footer/footer";

class blogsList extends React.Component {
  constructor(props) {
    super(props);
    this.state = { data: props.data };
    this.fetchData = this.fetchData.bind(this);
    this.makeList = this.makeList.bind(this);
  }
  getTime(timeinSec) {
    var utcSeconds = timeinSec;
    var d = new Date(0);
    d.setUTCSeconds(utcSeconds);
    return d.toDateString();
  }

  constructCard(data, i) {
    let url = "/blogs/" + cardTitle(data.title) + "/";
    return (
      <Link key={"card" + i} as={url} href="/blogs/[blogId]">
        <div className={i == 0 ? "card first" : "card"}>
          <h2>{data.title}</h2>
          <p className="date">{this.getTime(data.timestamp["_seconds"])}</p>
          {ReactHtmlParser(data.description)}
          {/* <div class="tc">
            <button class="offset blogPageReadMore">Read More</button>
          </div> */}
        </div>
      </Link>
    );
  }
  makeList() {
    let listView = [],
      cn = 0;
    for (let i = 0; i < this.state.data.length; i++) {
      listView.push(this.constructCard(this.state.data[i], cn));
      cn++;
    }
    return listView;
  }
  async fetchData() {
    let data = await fetch("/api/postsList");
    let response = await data.json();
    this.setState({ data: response["data"] });
  }
  componentDidMount() {
    if (!this.state.data) {
      this.fetchData();
    }
  }
  render() {
    let view = "";
    if (this.state.data) {
      view = (
        <>
          <BackButton />
          <div className="wrapper blogList">
            <div className="top"></div>
            <div className="content">{this.makeList()}</div>
          </div>
          <Footer />
        </>
      );
    } else {
      view = <Loader />;
    }
    return <>{view}</>;
  }
}
export default blogsList;
